import React from 'react';
import { Parallax } from 'react-parallax';
import './Parallax.css'; //   CSS  

const ParallaxImage = () => {
  return (
    <div className='all-para'>
      {/*     */}
      <div className="content-section">
        <h1 className="title">      </h1>
        <p className="subtitle">    !</p>
      </div>

      {/*    */}
      <Parallax bgImage="https://m.media-amazon.com/images/I/815VbplqFtL._AC_UL480_FMwebp_QL65_.jpg" strength={500}>
        <div className="parallax-content">
          <h1 className="parallax-title"> </h1>
          <p className="parallax-description">   !</p>
          <a href="#shop" className="cta-button"> </a>
        </div>
      </Parallax>

      {/*     */}
     
    </div>
  );
}

export default ParallaxImage;
